"use client";

import { useQuran, SurahDetail as SurahDetailType, Ayah } from "@/hooks/useQuran";
import { Card } from "@/components/ui/card";
import { Skeleton } from "@/components/ui/skeleton";
import Verse from "./Verse";
import AudioPlayer from "./AudioPlayer";

interface SurahDetailProps {
  surahId: string;
}

export default function SurahDetail({ surahId }: SurahDetailProps) {
  const { data, loading, error } = useQuran<SurahDetailType>(`/surah/${surahId}/quran-uthmani`);

  if (error) {
    return (
      <Card className="p-6">
        <p className="text-red-500">Error: {error}</p>
      </Card>
    );
  }

  if (loading) {
    return (
      <div className="space-y-4">
        <Card className="p-6">
          <Skeleton className="h-8 w-3/4 mb-4" />
          <Skeleton className="h-4 w-1/2" />
        </Card>
        {[...Array(5)].map((_, i) => (
          <Card key={i} className="p-6">
            <Skeleton className="h-6 w-full mb-2" />
            <Skeleton className="h-4 w-3/4" />
          </Card>
        ))}
      </div>
    );
  }

  const surah = data?.data;

  if (!surah) {
    return (
      <Card className="p-6">
        <p className="text-neutral-600">No surah data found.</p>
      </Card>
    );
  }

  return (
    <div className="space-y-6">
      <Card className="p-6">
        <div className="text-center">
          <h1 className="text-3xl font-bold mb-2">{surah.englishName}</h1>
          <p className="text-xl text-neutral-600 mb-4">
            {surah.englishNameTranslation}
          </p>
          <div className="flex justify-center items-center gap-4 text-sm text-neutral-500">
            <span>{surah.revelationType}</span>
            <span>•</span>
            <span>{surah.numberOfAyahs} verses</span>
          </div>
        </div>
      </Card>

      <AudioPlayer surahId={surahId} />

      <div className="space-y-4">
        {surah.ayahs.map((ayah: Ayah) => (
          <Verse key={ayah.number} verse={ayah} />
        ))}
      </div>
    </div>
  );
}
