import { useState, useEffect } from 'react';

interface QuranResponse<T> {
  code: number;
  status: string;
  data: T;
}

export interface SurahList {
  number: number;
  name: string;
  englishName: string;
  englishNameTranslation: string;
  numberOfAyahs: number;
  revelationType: string;
}

export interface Ayah {
  number: number;
  text: string;
  numberInSurah: number;
  juz: number;
  page: number;
}

export interface SurahDetail extends SurahList {
  ayahs: Ayah[];
}

export const useQuran = <T>(endpoint: string) => {
  const [data, setData] = useState<QuranResponse<T> | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch(`http://api.alquran.cloud/v1${endpoint}`);
        if (!res.ok) {
          throw new Error('Network response was not ok');
        }
        const json = await res.json();
        setData(json);
      } catch (err: any) {
        setError(err.message || 'An unexpected error occurred');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [endpoint]);

  return { data, loading, error };
};
